@import "https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@300;400;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Josefin Slab, serif;
}

body {
  background-color: #f0f0f0;
}

.hero-container {
  background-image: url("hero-image.5f258a63.jpg");
  background-size: cover;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 90vh;
  display: flex;
}

.hero-title h1 {
  color: #22a2b1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0 -6px #212121, 0 -6px #212121, 0 6px #212121, 0 6px #212121, -6px 0 #212121, 6px 0 #212121, -6px 0 #212121, 6px 0 #212121, -6px -6px #212121, 6px -6px #212121, -6px 6px #212121, 6px 6px #212121, -6px 18px #212121, 0 18px #212121, 6px 18px #212121, 0 19px 1px #0000001a, 0 0 6px #0000001a, 0 6px 3px #0000004d, 0 12px 6px #0003, 0 18px 18px #00000040, 0 24px 24px #0003, 0 36px 36px #00000026;
  flex-direction: column;
