  bottom: 18vh;
}

.homepage-welcome {
  flex-direction: row;
  width: 100%;
  height: 50vh;
  margin: 5vh 0;
  display: flex;
}

.homepage-welcome-text {
  color: #454545;
  justify-content: center;
  align-items: center;
  width: 100vh;
  margin: 0 5vh;
  font-size: 2.5vh;
  display: flex;
}

.homepage-image1 {
  background-image: url("homepage-image1.5c3aac5e.jpg");
}

.homepage-image2 {
  background-image: url("homepage-image2.4b4b7814.jpg");
}

.homepage-image3 {
  background-image: url("homepage-image3.e8eb64ad.jpg");
}

.homepage-image1, .homepage-image2, .homepage-image3 {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.calendar-window {
  grid-template-columns: 1fr 1fr;
  gap: 3%;
  padding: 5%;
  display: grid;
}

.calendar {
  border-right: 1px solid #aaa;
}

.calendar-upper {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 2%;
  display: flex;
}

.calendar-change-date {
  flex-direction: row;
  align-items: center;
  margin: 0 12%;
  display: flex;
}

.calendar-change-date div {
  cursor: pointer;
  margin: 0 15%;
  font-size: 5vh;
}

.calendar-weekdays-text, .calendar-date {
  text-align: center;
  grid-template-columns: repeat(7, 1fr);
  display: grid;
}

.calendar-weekdays-text div {
  color: #db0c0cda;
  width: 30px;
  height: 30px;
  font-size: 20px;
  font-weight: 900;
}

.current-month, .other-month, .current-date {
  cursor: pointer;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  line-height: 30px;
}

.current-month {
  color: #000;
  background-color: #f0f0f0;
}

.current-month:hover {
  color: #fff;
  background-color: #050505;
}

.other-month {
  color: #888;
  cursor: default;
  background-color: #f0f0f0;
}

.current-date {
  color: #fff;
  background-color: #007bff;
}

.current-date:hover {
  background-color: #0056b3;
}

.highlighted {
  color: #fff;
  background-color: #00a20dc9;
  border-radius: 50%;
}

.selected-date {
  color: #fff;
  background-color: #c8020f;
}

.today-schedule h3, .no-schedule {
  margin-top: 2%;
}

.event-list {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.event-list h2 {
  color: #333;
  margin-bottom: 10px;
  font-size: 1.5rem;
}

.event-list ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.event-list li {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 15px;
  padding: 10px;
  box-shadow: 0 1px 2px #0000001a;
}

.event-list h3 {
  color: #333;
  margin-bottom: 5px;
  font-size: 1.2rem;
}

.event-list p {
  color: #666;
  font-size: 1rem;
}

.login-container {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.login-content {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  padding: 40px;
  box-shadow: 0 4px 8px #0000001a;
}

.login-header {
  color: #333;
  margin-bottom: 10px;
  font-size: 2rem;
}

.login-subheader {
  color: #666;
  margin-bottom: 20px;
  font-size: 1rem;
}

.login-button {
  color: #fff;
  cursor: pointer;
  background-color: #db4437;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s ease-in-out;
}

.login-button:hover {
  background-color: #c43128;
}

.button-icon {
  margin-right: 10px;
}

.fab {
  vertical-align: middle;
  margin-right: 5px;
  font-size: 1.2rem;
}

.create-event {
  align-items: center;
  height: 100vh;
  display: flex;
}

.create-event-container {
  background-color: #f7f7f7;
  border-radius: 8px;
  width: 50vh;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.create-event-container h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

.create-event-container form {
  gap: 15px;
  display: grid;
}

.create-event-container label {
  font-size: 16px;
  font-weight: bold;
}

.create-event-container input, .create-event-container textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

.create-event-container textarea {
  resize: vertical;
}

.create-event-container button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.create-event-container button[type="submit"]:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  .create-event-container {
    padding: 15px;
  }
}

.error {
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: 90vh;
  margin: auto 5%;
  display: flex;
}

.error img {
  width: 30%;
  margin: 8% auto 2%;
  display: block;
}

.error h1 {
  font-family: Josefin Slab, sans-serif;
  font-style: 700;
  color: #b35fca;
  margin: 0% auto 2%;
  font-weight: bold;
}

.error p {
  font-family: Josefin Slab, sans-serif;
  font-style: 400;
  color: #464646;
  margin: 0% 6%;
  font-size: 2rem;
}

.footer-container {
  background-color: #f8f8f8;
  border-top: 1px solid #ccc;
  padding: 40px 0;
}

.footer-content {
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.footer-columns {
  flex-direction: row;
  display: flex;
}

.footer-column {
  flex: 1;
  padding: 0 15px;
}

.footer-column h2 {
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.footer-column ul {
  padding: 0;
  list-style: none;
}

.footer-column ul li {
  margin-bottom: 8px;
